import gql from 'graphql-tag';

const blogsQuery = gql`
  query allBlogs {
    blogs {
      id
      title
      content
    }
  }
`;

const careersQuery = gql`
  query allCareers {
    careers {
      id
      title
    }
  }
`;
export default {
  data: () => ({
  }),
  apollo: {
    blogs: {
      query: blogsQuery,
      loadingKey: 'loading',
    },
    careers: {
    query: careersQuery,
    loadingKey: 'loading',
    }
  },
};
